.tabBar {
  display: flex;
  background-color: #1e1e1e;
  height: 36px;
  border-bottom: 1px solid #333;
  align-items: center;
}

.tab {
  width: 140px; 
  padding: 0 16px;
  color: #cccccc;
  text-decoration: none;
  background-color: #2d2d2d;
  border-right: 1px solid #3c3c3c;
  height: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 14.5px;
  transition: background-color 0.2s ease;
  box-sizing: border-box;
}

.tab:hover {
  background-color: #3a3a3a;
}

.activeTab {
  width: 140px; 
  padding: 0 16px;
  color: #cccccc;
  text-decoration: none;
  border-top: 2px solid #bd93f9;
  background-color: #2d2d2d;
  border-right: 1px solid #3c3c3c;
  height: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 14.5px;
  transition: background-color 0.2s ease;
  box-sizing: border-box;
}
